righthead {
  lost-flex-container: row;
  lost-column: 3/4 2 2% flex;
  margin-bottom: 1%;
  margin-top: 1%; 
  telpanel {
    font-family: "PTSansNar"; 
    font-style: regular;
    font-weight: 400;
    justify-content: center;
    lost-flex-container: row;
    lost-column: 1/1 1 1% flex;

    div {
      text-align: center;
      lost-column: 1/3 3 3% flex;
      align-self: center;
    }
    margin-bottom: 1%;
    @util border-bottom-radius(0.6em);
  }



  headmenu {
    font-family: "UbuntuR"; 
    font-style: normal;
    font-weight: 400;
    font-style: bold;
   
    lost-column: 1/1 1 1% flex;
    margin-top: 1%;
     
    ul {
      list-style: none;    
       
      padding: 0;
      margin: 0;  
      li {
        display: inline-block;
      }
    }
  }
}

.tel {
  visibility: hidden;
} 